module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-carousel-basic',\n  template: `\n    <nz-carousel [nzEffect]=\"effect\">\n      <div nz-carousel-content *ngFor=\"let index of array\"><h3>{{index}}</h3></div>\n    </nz-carousel>`,\n  styles  : [\n      `[nz-carousel-content] {\n      text-align: center;\n      height: 160px;\n      line-height: 160px;\n      background: #364d79;\n      color: #fff;\n      overflow: hidden;\n    }\n\n    h3 {\n      color: #fff;\n    }\n    `\n  ]\n})\nexport class NzDemoCarouselBasicComponent {\n  array = [ 1, 2, 3, 4 ];\n  effect = 'scrollx';\n\n  ngOnInit() {\n    setTimeout(() => {\n      this.effect = 'fade';\n    }, 3000);\n  }\n}\n"